import React from "react";
import {
  Drawer,
  List,
  Divider,
  IconButton,
  ListItem,
  ListItemIcon,
  ListItemText,
  useTheme,
} from "@material-ui/core";
import { ChevronLeft, ChevronRight } from "@material-ui/icons";
import { useStyles } from "./NavMenu.styles";

export const NavMenu = ({
  openNabMenu,
  setOpenNabMenu,
  itemsCategory,
  itemsInfo,
}) => {
  const classes = useStyles();
  const theme = useTheme();
  return (
    <Drawer
      className={classes.drawer}
      variant="temporary"
      anchor="left"
      open={openNabMenu}
      classes={{
        paper: classes.drawerPaper,
      }}
    >
      <div className={classes.drawerHeader}>
        <IconButton onClick={() => setOpenNabMenu(false)}>
          {theme.direction === "ltr" ? <ChevronLeft /> : <ChevronRight />}
        </IconButton>
      </div>
      <Divider />
      <List>
        {itemsCategory.map((item, index) => (
          <ListItem button key={index}>
            <ListItemIcon>
              <img
                className={classes.icon}
                src={
                  process.env.PUBLIC_URL +
                  `/assets/images/category/${item.nameIcon}.svg`
                }
                alt=""
              />
            </ListItemIcon>
            <ListItemText primary={item.nameCategory} />
          </ListItem>
        ))}
      </List>
      <Divider />
      <List>
        {itemsInfo.map((item, index) => (
          <ListItem button key={index}>
            <ListItemIcon>
              <img
                className={classes.icon}
                src={
                  process.env.PUBLIC_URL +
                  `/assets/images/category/${item.nameIcon}.svg`
                }
                alt=""
              />
            </ListItemIcon>
            <ListItemText primary={item.nameInfo} />
          </ListItem>
        ))}
      </List>
    </Drawer>
  );
};
