import React, { useState } from "react";
import { CssBaseline } from "@material-ui/core";
import { useStyles } from "./MainWindow.styles";
import { ContentContainer } from "../Content/Content.container";
import { NavMenuContainer } from "../NavMenu/NavMenu.container";
import { HeaderContainer } from "../Header/Header.container";
import { Switch, Route, Link } from "react-router-dom";
import { LoginContainer } from "../Login/Login.container";
import { BasketContainer } from "../Basket/Basket.container";

export const MainWindow = () => {
  const classes = useStyles();
  const [openNabMenu, setOpenNabMenu] = useState(false);
  return (
    <div className={classes.root}>
      <CssBaseline />
      <Switch>
        <Route path="/" exact>
          <HeaderContainer
            openNabMenu={openNabMenu}
            setOpenNabMenu={setOpenNabMenu}
          />
          <NavMenuContainer
            openNabMenu={openNabMenu}
            setOpenNabMenu={setOpenNabMenu}
          />
          <ContentContainer openNabMenu={openNabMenu} />
        </Route>
        <Route path="/login" exact>
          <LoginContainer />
        </Route>
        <Route path="/basket" exact>
          <HeaderContainer
            openNabMenu={openNabMenu}
            setOpenNabMenu={setOpenNabMenu}
          />
          <NavMenuContainer
            openNabMenu={openNabMenu}
            setOpenNabMenu={setOpenNabMenu}
          />
          <BasketContainer />
        </Route>
      </Switch>
    </div>
  );
};
