import React, { useState } from "react";
import { Grid, Typography } from "@material-ui/core";
import { useCardStyles } from "./Content.styles";
import { ContentItemModal } from "./ContentItemModal";

export const ContentItem = ({ item, ...restProps }) => {
  const { nameItem, nameImage, description } = item;
  const classes = useCardStyles();
  const [isOpenModal, setIsOpenModal] = useState(false);
  return (
    <Grid item xl={2} lg={3} md={4} sm={6} xs={12}>
      <div className={classes.top}>
        <div className={classes.topBottomPadding}>
          <div className={classes.root} onClick={() => setIsOpenModal(true)}>
            <div>
              <img
                className={classes.media}
                src={
                  process.env.PUBLIC_URL +
                  `/assets/images/furniture/${nameImage}/${nameImage}1.png`
                }
                alt=""
              />
            </div>
            <div className={classes.content}>
              <div className={classes.nameItem}>
                <Typography gutterBottom variant="h5" component="h2">
                  {nameItem}
                </Typography>
              </div>
              <div className={classes.description}>{description}</div>
            </div>
          </div>
        </div>
      </div>
      <ContentItemModal
        isOpenModal={isOpenModal}
        setIsOpenModal={setIsOpenModal}
        item={item}
        {...restProps}
      />
    </Grid>
  );
};
