import React from "react";
import { Grid, Typography } from "@material-ui/core";
import { useStyles } from "./Basket.styles";

export const Basket = ({ items }) => {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <Grid container spacing={3} alignItems={"center"}>
        <Grid item xs={12}>
          <Typography
            variant="h2"
            style={{ color: "#e33371", paddingLeft: "15%" }}
          >
            Корзина
          </Typography>
        </Grid>
        {!items.length && (
          <Grid item style={{ paddingLeft: "25%" }}>
            Пусто
          </Grid>
        )}
        {items.map((item) => {
          const { id, nameItem, nameImage, description, price } = item;
          return (
            <>
              <Grid key={id} item xl={6} lg={6} md={6} sm={6} xs={6}>
                <img
                  style={{ width: "85%", paddingLeft: "25%" }}
                  src={
                    process.env.PUBLIC_URL +
                    `/assets/images/furniture/${nameImage}/${nameImage}1.png`
                  }
                  alt=""
                />
              </Grid>
              <Grid key={id} item xl={6} lg={6} md={6} sm={6} xs={6}>
                <div className={classes.description}>
                  <Typography gutterBottom variant="h6">
                    {nameItem}
                  </Typography>
                  <Typography
                    variant="body1"
                    color="textSecondary"
                    component="p"
                    style={{ height: "80px", overflow: "hidden" }}
                  >
                    {description}
                  </Typography>
                  <Typography
                    gutterBottom
                    variant="h5"
                    component="h2"
                    style={{ color: "#e33371" }}
                  >
                    Цена: {price} $
                  </Typography>
                </div>
              </Grid>
            </>
          );
        })}
      </Grid>
    </div>
  );
};
