import React from "react";
import { Field, reduxForm } from "redux-form";
import { TextField, Button } from "@material-ui/core";
import { useStyles } from "./Login.styles";
import { Redirect } from "react-router-dom";
import { fromValidator } from "../../utilities/form-validator";

const CustomField = ({ input, label, meta: { touched, error } }) => {
  const hasError = touched && error;
  return (
    <TextField
      label={label}
      variant="outlined"
      {...input}
      style={{
        display: "flex",
        marginBottom: "12px",
        width: "350px",
      }}
      error={hasError}
      helperText={hasError ? "Ошибка" : ""}
    />
  );
};

const LoginTemplate = ({ handleSubmit }) => {
  const classes = useStyles();
  return (
    <div className={classes.root}>
      <form onSubmit={handleSubmit} className={classes.fields}>
        <Field
          name="login"
          label="Логин"
          component={CustomField}
          validate={[fromValidator]}
        />
        <Field name="password" label="Пароль" component={CustomField} />
        <Button
          variant="contained"
          color="primary"
          type="submit"
          size="large"
          style={{ width: "350px" }}
        >
          Авторизироваться
        </Button>
      </form>
    </div>
  );
};

const LoginForm = reduxForm({ form: "login" })(LoginTemplate);

export const Login = ({ isInit, getUser }) => {
  const initializeUser = (values) => {
    getUser(values);
  };
  if (isInit) {
    return <Redirect to="/" />;
  }
  if (!isInit) {
    return <LoginForm onSubmit={initializeUser} />;
  }
};
