const GET_USER = "users/GET_USER";
const SET_ITEM_TO_BASKET = "users/SET_ITEM_TO_BASKET";
const DELETE_ITEM_TO_BASKET = "users/DELETE_ITEM_TO_BASKET";

const initial = {
  isInit: false,
  errorAutorization: false,
  usersData: [
    { login: "1", password: "1" },
    { login: "test2", password: "password2" },
  ],
  shoppingBasket: [],
};

export const usersReducer = (state = initial, action) => {
  switch (action.type) {
    case GET_USER:
      if (
        state.usersData.filter(
          (item) =>
            item.login === action.values.login && item.password === action.values.password
        ).length
      ) {
        return {
          ...state,
          isInit: true,
          errorAutorization: false,
        };
      } else {
        return {
          ...state,
          errorAutorization: true,
        };
      }
    case SET_ITEM_TO_BASKET:
      return {
        ...state,
        shoppingBasket: [...state.shoppingBasket, action.id],
      };
    case DELETE_ITEM_TO_BASKET:
      return {
        ...state,
        shoppingBasket: state.shoppingBasket.filter(
          (item) => item !== action.id
        ),
      };
    default:
      return state;
  }
};

export const getUser = (values) => ({ type: GET_USER, values });
export const setBasket = (id) => ({ type: SET_ITEM_TO_BASKET, id });
export const deleteBasket = (id) => ({ type: DELETE_ITEM_TO_BASKET, id });
