import React, { useState } from "react";
import { useModalStyles } from "./Content.styles";
import { Modal, Typography, IconButton, Button } from "@material-ui/core";
import CloseIcon from "@material-ui/icons/Close";
import ArrowBackIosIcon from "@material-ui/icons/ArrowBackIos";
import ArrowForwardIosIcon from "@material-ui/icons/ArrowForwardIos";
import "@google/model-viewer/dist/model-viewer";

export const ContentItemModal = ({
  isOpenModal,
  setIsOpenModal,
  item,
  setBasket,
}) => {
  const { nameItem, nameImage, nameModel, description, price } = item;
  const classes = useModalStyles();
  const [isButtonPressed, setIsButtonPressed] = useState(false);
  const [countPhoto, setCountPhoto] = useState(0);
  const getNextPhoto = (upDown) => {
    if (upDown) {
      if (countPhoto < 3) setCountPhoto(countPhoto + 1);
    } else {
      if (countPhoto > 0) setCountPhoto(countPhoto - 1);
    }
  };
  return (
    <Modal
      open={isOpenModal}
      onClose={() => setIsOpenModal(false)}
      className={classes.root}
    >
      <div className={classes.rootCard}>
        <div className={classes.close}>
          <IconButton size="large" onClick={() => setIsOpenModal(false)}>
            <CloseIcon fontSize="large" />
          </IconButton>
        </div>
        <div className={classes.imgBlock}>
          <IconButton onClick={() => getNextPhoto(false)}>
            <ArrowBackIosIcon fontSize="large" />
          </IconButton>
          {countPhoto ? (
            <img
              className={classes.img}
              src={
                process.env.PUBLIC_URL +
                `/assets/images/furniture/${nameImage}/${nameImage}${countPhoto}.png`
              }
              alt=""
            />
          ) : (
            <model-viewer
              style={{
                width: "60%",
              }}
              src={process.env.PUBLIC_URL + `/assets/models/${nameModel}.glb`}
              ios-src={process.env.PUBLIC_URL + `/assets/models/${nameModel}.usdz`}
              ar
              ar-modes="scene-viewer quick-look"
              ar-scale="auto"
              camera-controls
              alt="Model"
            ></model-viewer>
          )}
          <IconButton onClick={() => getNextPhoto(true)}>
            <ArrowForwardIosIcon fontSize="large" />
          </IconButton>
        </div>
        <div className={classes.content}>
          <Typography gutterBottom variant="h4" component="h2">
            {nameItem}
          </Typography>
          <Typography variant="body1" color="textSecondary" component="p">
            {description}
          </Typography>
        </div>
        <div className={classes.price}>
          <Typography
            gutterBottom
            variant="h5"
            component="h2"
            style={{ color: "#e33371" }}
          >
            Цена: {price} $
          </Typography>
          <Button
            variant="contained"
            color={isButtonPressed ? "primary" : "secondary"}
            disabled={isButtonPressed ? true : false}
            size="large"
            onClick={() => {
              setIsButtonPressed(true);
              setBasket(item);
            }}
          >
            В корзину
          </Button>
        </div>
      </div>
    </Modal>
  );
};
