import React from "react";
import clsx from "clsx";
import { AppBar, Toolbar, Typography, IconButton } from "@material-ui/core";
import { Menu } from "@material-ui/icons";
import { useStyles } from "./Header.styles";
import LoginIcon from "../../assets/images/login.svg";
import ShopBasket from "../../assets/images/shopping-basket.svg";
import { Link } from "react-router-dom";

export const Header = ({ openNabMenu, setOpenNabMenu, isInit }) => {
  const classes = useStyles();
  return (
    <>
      <AppBar
        position="fixed"
        className={clsx(classes.appBar, {
          [classes.appBarShift]: openNabMenu,
        })}
      >
        <Toolbar style={{ justifyContent: "space-between" }}>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            onClick={() => setOpenNabMenu(true)}
            edge="start"
            className={clsx(classes.menuButton, openNabMenu && classes.hide)}
          >
            <Menu />
          </IconButton>
          <Link to="/" style={{ textDecoration: "none", color: "white" }}>
            <Typography variant="h6" noWrap>
              Room in AR
            </Typography>
          </Link>
          {!isInit && (
            <Link to="/login">
              <IconButton>
                <img src={LoginIcon} alt="" className={classes.iconLogin} />
              </IconButton>
            </Link>
          )}
          {isInit && (
            <Link to="/basket">
              <IconButton>
                <img src={ShopBasket} alt="" className={classes.iconLogin} />
              </IconButton>
            </Link>
          )}
        </Toolbar>
      </AppBar>
    </>
  );
};
