import { makeStyles } from "@material-ui/core/styles";

export const useContentStyles = makeStyles((theme) => ({
  content: {
    flexGrow: 1,
    padding: theme.spacing(3),
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.leavingScreen,
    }),
    marginTop: "60px",
  },
  contentShift: {
    transition: theme.transitions.create("margin", {
      easing: theme.transitions.easing.easeOut,
      duration: theme.transitions.duration.enteringScreen,
    }),
  },
}));

export const useCardStyles = makeStyles(() => ({
  top: {
    width: "100%",
  },
  topBottomPadding: {
    position: "relative",
    width: "100%",
  },
  root: {
    margin: "0 auto",
    width: "100%",
    height: "100%",
    boxShadow: "0px 5px 10px 2px rgba(181, 195, 205, 0.2)",
    "&:hover": {
      backgroundColor: "rgba(181, 195, 205, 0.2)",
    },
  },
  media: {
    width: "100%",
  },
  content: {
    padding: "8px 16px 0px 16px",
  },
  description: {
    fontSize: "16px",
  },
}));

export const useModalStyles = makeStyles(() => ({
  root: {
    display: "flex",
    alignItems: "center",
    justifyContent: "center",
  },
  rootCard: {
    width: "95%",
    height: "95%",
    oveflow: "visible",
    backgroundColor: "#ffffff",
    borderRadius: "12px",
    overflow: "auto",
    position: "relative",
  },
  close: {
    display: "flex",
    justifyContent: "flex-end",
    margin: "10px 10px 5px 0px",
  },
  imgBlock: {
    display: "flex",
    justifyContent: "center",
  },
  img: {
    width: "60%",
    boxShadow: "0px 0px 10px 7px rgba(0, 0, 0, 0.2)",
  },
  content: {
    padding: "16px",
  },
  price: {
    display: "flex",
    justifyContent: "space-between",
    margin: "16px",
  },
}));

export const useCarouselStyles = makeStyles((theme) => ({
  root: {},
  img: {
    display: "flex",
    justifyContent: "content",
    height: "100%",
    width: "100%",
  },
}));
